@tailwind base;
@tailwind components;
@tailwind utilities;


.btn{
    @apply border-white w-fit text-white hover:border-sky-700 shadow-md  bg-sky-900 hover:bg-white hover:text-sky-900 hover:font-semibold 
}
.btnItem{
    @apply  py-1 px-2 rounded-br rounded-tr btn w-fit h-[40px]
}
.InputContainer{
    @apply flex rounded items-center
}

.inputItem{
    @apply placeholder:px-1 py-1 px-2 w-full rounded-bl rounded-tl h-[40px] focus:outline-none
}
.homeIndex{
    @apply flex w-full bg-red-800 p-3
}
.todoContainer{
    @apply flex flex-col gap-4 items-center justify-center
}
.todoText{
    @apply p-2 shadow-md rounded-md w-[50%] text-center font-semibold text-white text-xl bg-slate-900
}
.todoMsj{
    @apply flex w-[80%] gap-2 flex-col 
}
.mesaggeContainer{
    @apply flex mt-2 flex-col items-center gap-2 text-white w-full
}
.mesaggeList{
    @apply w-full items-center bg-zinc-900 shadow-lg rounded p-2  text-sm  justify-between flex
}
.mesaggeListItem{
    @apply flex gap-3 items-center
}
.mesaggeTest{
    @apply h-11 w-11 flex items-center justify-center rounded-full border-2 border-white
}
.delete{
    @apply p-2 rounded-md btn border-2 cursor-pointer
}
.homeContainer{
    @apply xl:px-64 lg:px-52 md:px-48 sm:px-32  h-[700px]
}
.homeContainerItem{
   @apply flex shadow-xl w-full h-full flex-col bg-gray-600 gap-8 p-3
}